<script>
import rest from '../rest'
import VueFileUpload from 'vue-file-upload'
import randomToken from 'random-token'
import vSelect from './VSelect.vue'
import VLoading from './VLoading.vue'
import sha1 from 'sha1'
import _ from 'lodash'
var localStorage = window.localStorage
var poId = randomToken(32)

export default{
  data () {
    return {
      loading: false,
      xb: [
        {value: '1', label: ''},
        {value: '0', label: ''}
      ],
      dengJi: [],
      myChildren: [],
      rclb: [],
      child: {},
      sqlb: [],
      rych: [],
      zydj: [],
      zgbmId: [],
      ryId: {},
      basicData: {
        flowEntityInfo: 'admin',
        flowVerId: 'BDB6AAC5734A2C5C3A44FA369A272E93',
        flowEntityUI: '/rccore/Rcpo/flowUI'
      },
      list: [],
      files:[],
      //
      fileList: [],
      filters:[
        {
          name:"imageFilter",
          fn (file) {
              var type = '|' + file.type.slice(file.type.lastIndexOf('/') + 1) + '|'

              return '|jpg|png|jpeg|bmp|gif|'.indexOf(type) !== -1
          }
        }
      ],
      //
      cbEvents:{
        onCompleteUpload: (file, response, status, header) => {
          console.log(response)
          console.log(file)
          console.log('finish upload')
          // this.getFileList()
        }
      },
      //xhr

      reqopts: {
        alias: 'fileData',
        formData: {
          'Encoding': 'utf-8',
          'Rpencoding': 'utf-8',
          '_x-requested-with': true,
          'ssoOpenId': this.user.ssoOpenId,
          'rcId': this.user.rcId,
          'useType': 'RYZS'
        },
        responseType: 'json',
        withCredentials: false
      }
    }
  },
  init () {
    this.user = JSON.parse(localStorage.getItem('baseInfo'))
    var me = this

    rest.getOptions('rcrd_cengci').then(res => {
      me.rclb = this.rebuildOptions(res)
    })
    rest.getOptions('rcgfbt_sqlb').then(res => {
      me.sqlb = this.rebuildOptions(res)
    })

  },
  ready () {
    // var me = this
    // me.loading = true
    // rest.post(this.user, {}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcpo/get').then(res => {
    //   me.loading = false
    //   console.log(res)
    //   me.basicData = res.data
    // })
    //
    // this.getList()
  },
  attached () {
  },
  methods: {
    closeModal() {
      $('#modal1').closeModal()
    },
    fileUploadUrl (useType) {
      return 'http://www.hzts.com.cn:8088/rcjk/rccore/RcpoFile/insert' + this.beforeUpload(useType)
    },
    // deleteItem (id) {
    //   rest.post(this.user, {ryId: id}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rych/delete').then(res => {
    //
    //     this.getList()
    //   })
    // },
    addChild() {
      this.child.cyId = randomToken(32)
      this.child.poId = poId
      this.child.saveInDatebase = true
      this.myChildren.push(this.child)
      $('#modal1').closeModal()

      this.child = {}
    },
    beforeUpload (useType) {
      var now = Date.now()
      var signature = ['NaRcJk4WeChat', now, '123332'].sort().join('')
      var timestamp = now
      var nonce = '123332'
      var encoding = 'utf-8'
      var rpencoding = 'utf-8'

      var r = {
        signature : sha1(signature),
        timestamp : timestamp,
        nonce : '123332',
        'ssoOpenId': this.user.ssoOpenId,
        'encoding' : 'utf-8',
        'rpencoding' : 'utf-8',
        '_x-requested-with' : true,
        useType: useType
      }
      var k = '?'
      Object.keys(r).forEach(v => {
        k = k + v + '=' + r[v] + '&'
      })

      return k
    },
    getList () {
      var me = this

      rest.post(this.user, {}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcpo/page').then(res => {
        this.list = res.datas
      })
    },
    rebuildOptions (options) {
      var report = []

      options.forEach(option => {
        var item = {
          label: option.itemName,
          value: option.itemCode
        }
        report.push(item)
      })

      return report
    },
    onStatus (file) {
      if(file.isSuccess){
        return ""
      }else if(file.isError){
        return ""
      }else if(file.isUploading){
        return ""
      }else{
        return ""
      }
    },
    uploadItem (file) {
      file.upload()
    },
    submitData (e) {
      e.preventDefault()
      var me = this

      this.basicData.poId = poId
      this.basicData.flowEntityId = this.basicData.poId
      this.basicData.isAdd = true
      this.loading = true
      this.basicData.qtcyInfo = this.myChildren
      this.basicData.qtcyInfo = JSON.stringify(this.basicData.qtcyInfo)

      rest.post(this.user, this.basicData, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcpo/entitySave').then(res => {
        me.loading = false
        if (!res.success) return Materialize.toast(res.message, 4000)
        Materialize.toast('', 2000)
      })
    },
    getSrc (fileId) {
      var now = Date.now()
      var signature = ['NaRcJk4WeChat', now, '123332'].sort().join('')
      var timestamp = now
      var nonce = '123332'
      var query = {
        signature : sha1(signature),
        timestamp : timestamp,
        nonce : '123332',
        'ssoOpenId': this.user.ssoOpenId,
        'rcId': this.user.rcId,
        refId: fileId
      }
      var r = 'http://www.hzts.com.cn:8088/rcjk/rccore/RcxxFile/download?'

      Object.keys(query).forEach(key => {

        r = r + key + '=' + query[key] + '&'
      })

      return r
    },
    modal () {
      $('#modal1').openModal()
    },
    getFileList () {
      var me = this

      rest.post(this.user, {useType: 'RYZS'}, 'http://www.hzts.com.cn:8088/rcjk/rccore/RcxxFile/fileList').then(res => {

        me.fileList = res.datas
      })
    }
  },
  components: {
    VueFileUpload,
    vSelect,
    VLoading
  }
}
</script>
<style scoped>
.content {
  padding: 0 20px;
}
.card-image {
  text-align: center;
  min-height: 150px;
  padding-top: 55px;
}
.fileupload-button {
  background-color: #666 !important;
  width: 100%;
}
.btn-add {
  position: fixed;
  bottom: 30px;
  right: 30px;
}
.modal {
  width: 100%;
}
.btn-up {
  width: 100%;
  background-color: transparent;
  box-shadow: none;
}
</style>



