<template lang="jade">
.dad
  v-loading(:show='loading')
  form.col.content
    .input-field.col.s10
      i.fa.fa-mobile-phone.prefix
      input.validate(type="text" v-model='user.mobilePhone' placeholder='')
      label 
    .input-field.col.s10
      i.fa.fa-credit-card.prefix
      input.validate(type="text" v-model='user.lxrZj' placeholder='')
      label 
    .input-field.col.s10
      i.fa.fa-user.prefix
      input.validate(type="text" v-model='user.rcName' placeholder='')
      label 
    //- .input-field.col.s10
    //-   i.fa.fa-wechat.prefix
    //-   input.validate(type="text" v-model='user.wcOpenId' placeholder='')
    //-   label 
    .input-field.col.s10
      i.fa.fa-lock.prefix
      input.validate(type="password" v-model='user.pwd' placeholder='')
      label 
    //- .verifyCode
    //-   button.waves-effect.waves-light.btn(@click='getCode') {{verifyBtn}}
    //-   input.validate(type="text" v-model='user.codePhone' placeholder='')
    .button(style='margin-top: 50px !important')
      a.waves-effect.waves-light.btn(@click='signUp') 
      a.signUp.waves-effect(v-link="{ path: '/Login' }", style='height: 36px; text-align: center;line-height: 36px;') 
</template>

<script>
import rest from '../rest'
import VLoading from './VLoading.vue'
var sha1 = require('sha1')

export default {
  data() {
    return {
      loading: false,
      user: {
        mobilePhone: '',
        lxrZj: '',
        wcOpenId: ''
      },
      verifyBtn: ''
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    signUp() {
      this.user.password = sha1(this.user.pwd)
      rest.post({}, this.user, 'http://www.hzts.com.cn:8088/rcjk/rccore/WeChatUser/noneToInsert').then(res => {
        if (!res.success) return Materialize.toast(res.message, 4000)
        var baseInfo = {
          ssoOpenId: res.data.rcId,
          username: res.data.ssoLoginName,
          rcId: res.data.rcId
        }

        localStorage.setItem('baseInfo', JSON.stringify(baseInfo))
        return this.$router.go({name: 'Home'})
      })
    },
    getCode() {
      if (!this.user.mobilePhone || !this.user.lxrZj) {
        return Materialize.toast('', 4000)
      }
      this.loading = true
      rest.post({}, {mobilePhone: this.user.mobilePhone, lxrZj: this.user.lxrZj}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Register/getCode').then(res => {
        if (!res.success) {
          return Materialize.toast(res.message, 4000)
        }
        this.verifyBtn = ''
        this.loading = false
      })
    },
  },
  components: {
    VLoading
  }
};
</script>

<style lang="css" scoped>
.dad {
  width: 100%;
  padding: 20px 0;
  height: 100%;
  line-height: 100%;
}
.content {
  /*display: inline;*/
  width: 84%;
  /*padding: 20px;*/
  padding-top: 20px;
  margin-left: 8%;
  /*text-align: center;*/
}
.verifyCode {
  width: 100%;
}
.verifyCode input{
  float: right;
  width: 45%;
}
.verifyCode button{
  float: right;
  width: 40%;
  background-color: #f9f9f9;
  color: #666;
  box-shadow: none;
  border: 1px solid #999;
  margin-top: 7px;
  margin-left: 5px
}
.button {
  position: relative;
  width: 80%;
  margin-left: 10%;
}
.button button{
  /*position: absolute;
  right: 0;*/
  width: 100%;
}
.button a{
  /*position: absolute;
  left: 0;*/
  width: 100%;

}
.signUp {
  width: 100%;
  margin-top: 30px;
  display: block;
  color: #666;
}
</style>



