<template lang="jade">
.row
  v-loading(:show='loading')
  form.col.s12.content
    .input-field.col.s12
      input.validate(type="text" v-model='basicData.sqzz' placeholder='')
      label.active 
    .col.s12
      label.active 

      v-select(:value.sync='basicData.sqcc', :options='sqcc')
    br
    br
    table
      thead
        tr
          th(style='text-align: center') 
          th(style='text-align: center') 
          th(style='text-align: center') 
          //- th(style='text-align: center') action
      tbody
        tr(v-for='file in files', style='text-align: center')
          td(v-text='file.name', style='text-align: center')
          td(v-text='file.progress', style='text-align: center')
          td(v-text='onStatus(file)', style='text-align: center')
          //- td(style='text-align: center')
          //-   button(type='button',@click="uploadItem(file)") 
    a.btn.btn-up
      vue-file-upload(v-bind:url='fileUploadUrl("ZZCL")',
        v-bind:files.sync = 'files',
        v-bind:filters = "filters",
        v-bind:events = 'cbEvents',
        v-bind:request-options = "reqoptsZZCL",
        name='fileData',
        label=''
        )
    br
    br
    a.btn.btn-up
      vue-file-upload(v-bind:url='fileUploadUrl("LDHTS")',
        v-bind:files.sync = 'files',
        v-bind:filters = "filters",
        v-bind:events = 'cbEvents',
        v-bind:request-options = "reqoptsLDHTS",
        name='fileData',
        label=''
        )

    button.waves-effect.waves-light.btn(@click='submitData') 

</template>
<script>
import rest from '../rest'
import VueFileUpload from 'vue-file-upload'
import randomToken from 'random-token'
import vSelect from './VSelect.vue'
import VLoading from './VLoading.vue'
import sha1 from 'sha1'
import _ from 'lodash'
var localStorage = window.localStorage
var sqId = randomToken(32)
export default{
  data () {
    return {
      loading: false,
      dengJi: [],
      sqcc: [],
      rych: [],
      zydj: [],
      zgbmId: [],
      ryId: {},
      basicData: {
        sqId: sqId,
        flowEntityInfo: 'admin',
        flowVerId: 'BDB6AAC5734A2C5C3A44FA369A272E93',
        flowEntityUI: '/rccore/Rcrd/flowUI'
      },
      list: [],
      files:[],
      //
      fileList: [],
      filters:[
        {
          name:"imageFilter",
          fn (file) {
              var type = '|' + file.type.slice(file.type.lastIndexOf('/') + 1) + '|'

              return '|jpg|png|jpeg|bmp|gif|'.indexOf(type) !== -1
          }
        }
      ],
      //
      cbEvents:{
        onCompleteUpload: (file, response, status, header) => {
          console.log(response)
          console.log(file)
          console.log('finish upload')
          // this.getFileList()
        }
      },
      //xhr

      reqoptsZZCL: {
        alias: 'fileData',
        formData: {
          'Encoding': 'utf-8',
          'Rpencoding': 'utf-8',
          '_x-requested-with': true,
          'ssoOpenId': this.user.ssoOpenId,
          'sqId': sqId,
          'useType': 'ZZCL'
        },
        responseType: 'json',
        withCredentials: false
      },
      reqoptsLDHTS: {
        alias: 'fileData',
        formData: {
          'Encoding': 'utf-8',
          'Rpencoding': 'utf-8',
          '_x-requested-with': true,
          'ssoOpenId': this.user.ssoOpenId,
          'sqId': sqId,
          'useType': 'LDHTS'
        },
        responseType: 'json',
        withCredentials: false
      }
    }
  },
  init () {
    this.user = JSON.parse(localStorage.getItem('baseInfo'))

    var me = this
    rest.getOptions('rcrd_cengci').then(res => {
      me.sqcc = this.rebuildOptions(res)
    })

  },
  ready () {
    // var me = this
    // me.loading = true
    // rest.post(this.user, {}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcpo/get').then(res => {
    //   me.loading = false
    //   console.log(res)
    //   me.basicData = res.data
    // })
    //
    // this.getList()
  },
  attached () {
  },
  methods: {
    fileUploadUrl (useType) {
      return 'http://www.hzts.com.cn:8088/rcjk/rccore/RcrdFile/insert' + this.beforeUpload(useType)
    },
    // deleteItem (id) {
    //   rest.post(this.user, {ryId: id}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rych/delete').then(res => {
    //
    //     this.getList()
    //   })
    // },
    beforeUpload (useType) {
      var now = Date.now()
      var signature = ['NaRcJk4WeChat', now, '123332'].sort().join('')
      var timestamp = now
      var nonce = '123332'
      var encoding = 'utf-8'
      var rpencoding = 'utf-8'

      var r = {
        signature : sha1(signature),
        timestamp : timestamp,
        nonce : '123332',
        'ssoOpenId': this.user.ssoOpenId,
        'encoding' : 'utf-8',
        'rpencoding' : 'gbk',
        '_x-requested-with' : true,
        'sqId': sqId,
        useType: useType
      }
      var k = '?'
      Object.keys(r).forEach(v => {
        k = k + v + '=' + r[v] + '&'
      })

      return k
    },
    getList () {
      var me = this

      rest.post(this.user, {}, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcpo/page').then(res => {
        this.list = res.datas
      })
    },
    rebuildOptions (options) {
      var report = []

      options.forEach(option => {
        var item = {
          label: option.itemName,
          value: option.itemCode
        }
        report.push(item)
      })

      return report
    },
    onStatus (file) {
      if(file.isSuccess){
        return ""
      }else if(file.isError){
        return ""
      }else if(file.isUploading){
        return ""
      }else{
        return ""
      }
    },
    uploadItem (file) {
      file.upload()
    },
    submitData (e) {
      e.preventDefault()
      var me = this

      this.basicData.flowEntityId = this.basicData.sqId
      this.basicData.isAdd = true
      this.loading = true

      rest.post(this.user, this.basicData, 'http://www.hzts.com.cn:8088/rcjk/rccore/Rcrd/entitySave').then(res => {
        me.loading = false
        if (!res.success) return Materialize.toast(res.message, 4000)
        Materialize.toast(',', 2000)

        this.files.forEach(file => {
          file.upload()
        })
      })
    },
    getSrc (fileId) {
      var now = Date.now()
      var signature = ['NaRcJk4WeChat', now, '123332'].sort().join('')
      var timestamp = now
      var nonce = '123332'
      var query = {
        signature : sha1(signature),
        timestamp : timestamp,
        nonce : '123332',
        'ssoOpenId': this.user.ssoOpenId,
        'rcId': this.user.rcId,
        refId: fileId
      }
      var r = 'http://www.hzts.com.cn:8088/rcjk/rccore/RcxxFile/download?'

      Object.keys(query).forEach(key => {

        r = r + key + '=' + query[key] + '&'
      })

      return r
    },
    modal () {
      $('#modal1').openModal()
    },
    getFileList () {
      var me = this

      rest.post(this.user, {useType: 'RYZS'}, 'http://www.hzts.com.cn:8088/rcjk/rccore/RcxxFile/fileList').then(res => {

        me.fileList = res.datas
      })
    }
  },
  components: {
    VueFileUpload,
    vSelect,
    VLoading
  }
}
</script>
<style scoped>
.content {
  padding: 0 20px;
}
.card-image {
  text-align: center;
  min-height: 150px;
  padding-top: 55px;
}
.fileupload-button {
  background: transparent !important;
  height: 100px !important;
  border: 2px solid #26a69a !important;
  color: #26a69a !important;
  line-height: 100px !important;
  border-style: dashed !important;
  border-radius: 5px !important;
  width: 100%;
}
.btn-add {
  position: fixed;
  bottom: 30px;
  right: 30px;
}
.modal {
  width: 100%;
}
.btn-up {
  width: 100%;
  padding: 0;
  height: 100px;
  background-color: transparent;
  box-shadow: none;
}
</style>



